import { Component, h, Event, EventEmitter, Prop } from '@stencil/core';
import { Items } from './breadcrumbs.types';

/**
 * A Breadcrumb consists of a list of distinct "places" that user has gone through,
 * before ending up where they are right now, in a website or an application.
 *
 * These "places" can be for example _pages_ of a website, which are hierarchically
 * before the current page that the user is looking at.
 * They could also be _steps_ which the user has gone through, which perhaps have no
 * hierarchal relation with each other, but has eventually led the user "here".
 *
 * :::note
 * - Where the user currently is, is always the last step of the breadcrumb.
 * - A breadcrumbs never shows where users can go after this place.
 * It only illustrates where user has been before ending up here.
 * If the path that a user can take is not changing and if next steps are clear,
 * you can use the [Progress flow component](#/component/limel-progress-flow) instead.
 * :::
 *
 * Breadcrumbs are often placed horizontally before the main content of the current screen.
 * @exampleComponent limel-example-breadcrumbs-basic
 */

@Component({
    tag: 'limel-breadcrumbs',
    shadow: true,
    styleUrl: 'breadcrumbs.scss',
})
export class Breadcrumbs {
    @Prop()
    public items: Items[];

    /**
     * Fired when clicking on the flow item
     */
    @Event()
    public interact: EventEmitter<void>;

    public render() {
        return (
            <nav aria-label="Breadcrumb">
                <ol>
                    {this.renderSteps(this.items)}
                    {this.renderLastStep(this.items)}
                </ol>
            </nav>
        );
    }

    private renderSteps = (items) => {
        console.log(items[0].link);
        const allStepsWithoutLast = items.slice(0, -1);

        if (items[0].link) {
            return allStepsWithoutLast.map(this.renderLinks);
        }

        return allStepsWithoutLast.map(this.renderButtons);
    };

    private renderButtons = (items) => {
        return (
            <li>
                <button onClick={this.handleClick}>
                    <limel-icon name={items.icon} />
                    <span>{items.text}</span>
                </button>
            </li>
        );
    };

    private renderLinks = (items) => {
        return (
            <li>
                <a href={items.link}>
                    <limel-icon name={items.icon} />
                    <span>{items.text}</span>
                </a>
            </li>
        );
    };

    private renderLastStep = (items) => {
        const lastItem = items.slice(-1);

        return (
            <li>
                <limel-icon name={lastItem[0].icon} size="x-small" />
                <span aria-current="page">{lastItem[0].text}</span>
            </li>
        );
    };

    private handleClick = () => {
        this.interact.emit();
    };
}
